import { LoadingOutlined } from '@ant-design/icons';
import { Button } from 'antd';
import React, { useEffect, useState } from 'react';

import { useStorageContext } from '../Storage';
import { useFirebase } from '../../firebase';
import { useCurrentUser } from '../auth/useCurrentUser';

export const DataPage = () => {
  const currentUser = useCurrentUser();
  const firebase = useFirebase();
  const storage = useStorageContext();

  const [isPersisted, setIsPersisted] = useState<boolean>();

  const checkIsPersisted = () => {
    firebase.firestore().collection('users').doc(currentUser.uid).get().then((docSnapshot) => {
      setIsPersisted(docSnapshot.exists);
    });
  }
  function persistToServer() {
    firebase.firestore().collection('users').doc(currentUser.uid).set(storage.data);
  }

  useEffect(() => {
    checkIsPersisted();
  });

  if (isPersisted === undefined) {
    return <LoadingOutlined />;
  }

  if (!isPersisted) {
    return (
      <>
        Your Data has been synced to your account: {currentUser.displayName}
        {currentUser.photoURL && <img src={currentUser.photoURL} alt={currentUser.displayName ? currentUser.displayName : undefined} />}
      </>
    );
  }

  return <Button onClick={persistToServer}>Persist Data to your account: {currentUser.displayName}</Button>;
};
