import React, { FC } from 'react';
import { StatisticPerQuiz, CharacterSummary } from './interfaces';
import { Info } from '../../components/Info';
import { Table } from 'antd';
import { ColumnType } from 'antd/lib/table';
import { renderDate, renderPercent, renderDuration } from '../../helpers/formatter';
import { dictionary } from '../../modules/character/dictionary';
import { USER_VOCABS } from '../../modules/userVocab/data';

const DETAIL_COLUMN: ColumnType<StatisticPerQuiz>[] = [
  {
    title: 'Date',
    dataIndex: 'timestamp',
    render: renderDate,
  },
  {
    title: 'Mistakes',
    dataIndex: 'mistakeCount',
    align: 'right'
  },
  {
    title: 'Peeks',
    dataIndex: 'peekCount',
    align: 'right'
  },
  {
    title: 'Accuracy',
    dataIndex: 'accuracy',
    render: renderPercent
  }
];

interface Props {
  summary: CharacterSummary;
}

export const CharacterDetailSummary: FC<Props> = ({ summary }) => {
  const vocabs = USER_VOCABS.filter((word) => word.hanzi.includes(summary.character));
  return (
    <>
      <div className="d-flex mb-md">
        <div className="hanzi-md">{summary.character}</div>
      </div>
      <div className="my-md">
        <Info title="Pīnyīn">
          {dictionary[summary.character].pinyin}
        </Info>
        <Info title="Definition">
          {dictionary[summary.character].definition}
        </Info>
        <Info title="Words in Bank">
          <div className="d-flex">
            {vocabs.map((vocab) => vocab.hanzi).join(', ')}
          </div>
        </Info>
      </div>
      <div className="my-md">
        <div className="d-flex my-xs f-wrap">
          <Info title="Quiz Taken" className="f-grow f-33">
            {summary.statistic.quizCount}
          </Info>
          <Info title="Perfect Count" className="f-grow f-33">
            {summary.statistic.perfectQuizCount}
          </Info>
          <Info title="Avg. Time Taken" className="f-grow f-33">
            {renderDuration(summary.statistic.timeTakenAveragePerQuiz)}
          </Info>
          <Info title="Avg. Peek Count" className="f-grow f-33">
            {summary.statistic.peekCountAveragePerQuiz}
          </Info>
          <Info title="Avg. Mistakes" className="f-grow f-33">
            {summary.statistic.mistakesAveragePerQuiz}
          </Info>
          <Info title="Accuracy" className="f-grow f-33">
            {renderPercent(summary.statistic.accuracy)}
          </Info>
        </div>
      </div>
      <Table size="small" dataSource={summary.statisticsPerQuiz} columns={DETAIL_COLUMN} pagination={false} />
    </>
  );
}
