import { Record } from '../../hooks';
import { QuizRecords, QuizData } from '../../QuizRecord';
import { StatisticPerQuiz, StatisticPerCharacter, CharacterSummary } from './interfaces';
import { lastItem, sumBy } from '../../helpers/math';

export function calculateStatisticSummary(records: QuizRecords) {
  const recordsGroupedPerCharacter = groupRecordsByCharacter(records);
  const characterSummaries = calculateCharacterSummaries(recordsGroupedPerCharacter);

  return {
    quizCount: records.length,
    characterCount: characterSummaries.length,
    characterSummaries,
    familiarityDistribution: calculateFamiliarityDistribution(characterSummaries)
  }
}

function calculateFamiliarityDistribution(characterSummaries: CharacterSummary[]) {
  let alien = 0;
  let forgotten = 0;
  let familiar = 0;
  let remember = 0;

  characterSummaries.forEach((summary) => {
    const accuracy = summary.statistic.accuracy;
    if (accuracy < 0.5) {
      alien++;
    } else if (accuracy < 0.75) {
      forgotten++;
    } else if (accuracy < 0.90) {
      familiar++;
    } else {
      remember++;
    }
  });

  return { alien, forgotten, familiar, remember };
}

function groupRecordsByCharacter(records: QuizRecords) {
  return records.reduce<Dictionary<StatisticPerQuiz[]>>((statistics, record) => {
    statistics[record.data.character] = statistics[record.data.character] || [];
    statistics[record.data.character].push(
      calculateStatisticPerRecord(record)
    );
    return statistics;
  }, {});
}

function calculateCharacterSummaries(statisticsPerQuiz: Dictionary<StatisticPerQuiz[]>): CharacterSummary[] {
  return Object.entries(statisticsPerQuiz)
    .map(([character, statisticsPerQuiz]) => {
      return {
        character,
        statistic: calculateStatisticPerCharacter(statisticsPerQuiz),
        statisticsPerQuiz
      }
    });
}

function calculateStatisticPerCharacter(statisticsPerQuiz: StatisticPerQuiz[]): StatisticPerCharacter {
  const sumMistakeCount = sumBy(statisticsPerQuiz, 'mistakeCount');
  const sumPeekCount = sumBy(statisticsPerQuiz, 'peekCount');
  const sumPeekTime = sumBy(statisticsPerQuiz, 'peekTime');
  const sumTimeTaken = sumBy(statisticsPerQuiz, 'timeTaken');
  const sumAccuracy = sumBy(statisticsPerQuiz, 'accuracy');

  const perfectQuizCount = statisticsPerQuiz
    .filter((quizStatistic) => quizStatistic.mistakeCount === 0 && quizStatistic.peekCount === 0)
    .length;
  const quizCount: number = statisticsPerQuiz.length;

  return {
    quizCount,
    mistakesAveragePerQuiz: sumMistakeCount / quizCount,
    accuracy: sumAccuracy / quizCount,
    peekCountAveragePerQuiz: sumPeekCount / quizCount,
    peekTimeAveragePerQuiz: sumPeekTime / quizCount,
    timeTakenAveragePerQuiz: sumTimeTaken / quizCount,
    perfectQuizCount
  };
}

function calculateStatisticPerRecord(record: Record<QuizData>) {
  const timeStarted = Math.min(
    lastItem(record.data.correctStrokes).timestamp,
    lastItem(record.data.mistakes)?.timestamp || Infinity,
    lastItem(record.data.peeks)?.timestamp || Infinity,
  );

  const accuracyFactors =
    record.data.mistakes.length +
    record.data.correctStrokes.length +
    record.data.peeks.length * record.data.correctStrokes.length / 2;

  return {
    accuracy: record.data.correctStrokes.length / accuracyFactors,
    timeTaken: record.timestamp - timeStarted,
    mistakeCount: record.data.mistakes.length,
    peekCount: record.data.peeks.length,
    peekTime: sumBy(record.data.peeks, 'data')
  };
}
