import { ExpandOutlined } from '@ant-design/icons';
import { Table, Modal, Button } from 'antd';
import { ColumnType } from 'antd/lib/table';
import React, { FC } from 'react';

import { CharacterDetailSummary } from './CharacterDetailSummary';
import { renderPercent } from '../../helpers/formatter';
import { CharacterSummary } from './interfaces';

const SUMMARY_COLUMN: ColumnType<CharacterSummary>[] = [
  {
    title: '字',
    dataIndex: 'character',
    align: 'center',
  },
  {
    title: 'Quiz Taken',
    dataIndex: ['statistic', 'quizCount'],
    key: 'statistic.quizCount',
    align: 'right'
  },
  {
    title: 'Perfect Count',
    dataIndex: ['statistic', 'perfectQuizCount'],
    align: 'right'
  },
  {
    title: 'Avg. Accuracy',
    dataIndex: ['statistic', 'accuracy'],
    key: 'statistic.accuracy',
    align: 'right',
    defaultSortOrder: 'ascend',
    sorter: (a, b) => a.statistic.accuracy - b.statistic.accuracy,
    sortDirections: ['ascend', 'descend'],
    render: renderPercent,
  },
  {
    title: '',
    key: 'action',
    render: (text, record) => {
      const showDetail = () => {
        return Modal.confirm({
          type: undefined,
          content: <CharacterDetailSummary summary={record} />,
          maskClosable: true,
          okCancel: false
        });
      };
      return (
        <Button type="link" color="default" onClick={showDetail} icon={<ExpandOutlined />} />
      );
    },
  },
];

interface Props {
  characterSummaries: CharacterSummary[];
}

export const CharacterSummariesTable: FC<Props> = ({ characterSummaries: statistics }) => {
  return (
    <Table size="small" pagination={{ pageSize: 50, hideOnSinglePage: true }} dataSource={statistics} columns={SUMMARY_COLUMN} />
  );
};
