export function roundDecimal(x: number, decimals: number) {
  const modifier = pow(10, decimals);
  return Math.round(x * modifier) / modifier;
}

export function pow(x: number, power: number): number {
  if (power === 0) {
    return 1;
  }
  return x * pow(x, power - 1);
}

export function select<T>(records: T[], key: keyof T) {
  return records.map((record) => record[key]);
}

export function sumBy<T>(records: T[], key: keyof T) {
  return sum(select(records, key) as unknown as number[]);
}

export function sum(items: number[]) {
  return items.reduce((result, record) => result + record, 0);
}

export function avg(items: number[]) {
  if (items.length === 0) {
    return 0;
  }
  return sum(items) / items.length;
}

export function lastItem<T>(array: T[]) {
  return array[array.length - 1];
}

export function randomItem<T>(items: T[]) {
  const randomIndex = Math.floor(Math.random() * items.length);
  return items[randomIndex];
}
