import { useState } from 'react';
import { useStorage } from './Storage';

export function useToggle(defaultValue = false): [boolean, VoidFunction] {
  const [toggle, setToggle] = useState<boolean>(defaultValue);
  return [toggle, () => setToggle((current) => !current)];
}

export function useCounter(defaultValue = 0): [number, VoidFunction, VoidFunction] {
  const [counter, setCounter] = useState<number>(defaultValue);
  return [counter, () => setCounter((current) => ++current), () => setCounter(defaultValue)];
}

export interface Record<T> {
  timestamp: number;
  data: T;
}

export function useRecorder<T>(initialValue: Record<T>[] = []): [Record<T>[], (record: T) => Record<T>[], VoidFunction] {
  const [records, setRecords] = useState<Record<T>[]>(initialValue);

  const saveRecord = (data: T) => {
    const record: Record<T> = {
      timestamp: new Date().getTime(),
      data,
    }
    const newRecords = [record, ...records];
    setRecords(newRecords);

    return newRecords;
  };

  const reset = () => setRecords([]);

  return [records, saveRecord, reset];
}

export function useTimer(): [boolean, Record<number>[], VoidFunction, VoidFunction, VoidFunction] {
  const [startTime, setStartTime] = useState(0);
  const [records, saveRecord, resetRecord] = useRecorder<number>();
  const isPeeking = startTime !== 0;
  const start = () => {
    setStartTime(new Date().getTime());
  };
  const end = () => {
    if (isPeeking) {
      saveRecord(new Date().getTime() - startTime);
      setStartTime(0);
    }
  };
  const reset = () => {
    resetRecord();
    setStartTime(0);
  };

  return [isPeeking, records, start, end, reset];
}

export function useStoredRecorder<T>(storageKey: string): [Record<T>[], (data: T) => void] {
  const [storage, setStorage] = useStorage<Record<T>[]>(storageKey);
  const [records, record] = useRecorder(storage);

  const recordAndSave = (data: T) => {
    const newRecords = record(data);
    setStorage(newRecords);
  };

  return [records, recordAndSave];
}
