import classnames from 'classnames';
import React, { FC } from 'react';

interface InfoProps {
  title: string;
  className?: string;
}

export const Info: FC<InfoProps> = ({ title, className, children }) => {
  return (
    <div className={classnames("my-xs", className)}>
      <div className="text-light">{title}:</div>
      {children}
    </div>
  );
}
