import React, { FC } from 'react';
import { Card } from 'antd';

import { dictionary } from '../modules/character/dictionary';
import { Radical } from '../modules/radical/radicals';
import { Info } from './Info';

interface RadicalCardProps {
  radical: Radical;
  className?: string;
}

export const RadicalCard: FC<RadicalCardProps> = ({ className, radical }) => {
  const mainVariant = radical.variants[0];
  const otherVariants = radical.variants.slice(1);
  const detail = dictionary[mainVariant.character];
  return (
    <Card className={className} title={
      <div className="d-flex">
        <div className="text-light">#{radical.id}</div>
      </div>
    }>
      <div className="d-flex f-stretch">
        <div className="hanzi-lg mr-md mb-md">
          {mainVariant.character}
        </div>
        <div className="f-grow">
          <div className="d-flex">
            <Info title="Pīnyīn" className="f-grow">
              {detail.pinyin.join('; ')}
            </Info>
            <Info title="Strokes" className="f-grow">
              {mainVariant.strokes}
            </Info>
          </div>
          {!!otherVariants.length && (
            <Info title="Variants">
              <div className="d-flex">
                {otherVariants.map((variant) =>
                  <span key={variant.character} className="hanzi-xs">
                    {variant.character}
                  </span>
                )}
              </div>
            </Info>
          )}
        </div>
      </div>
      <Info title="Definition">
        {detail.definition}
      </Info>
    </Card>
  );
};
