import classnames from 'classnames';
import { Button } from 'antd';
import React, { useState } from 'react';

import { Writer } from '../../modules/character/Writer';
import Title from 'antd/lib/typography/Title';
import { useRandomVocab } from './useRandomVocab';
import { useQuizRecorder } from '../../QuizRecord';
import { EyeFilled, EyeInvisibleFilled } from '@ant-design/icons';

export const QuizPage = () => {
  const [vocab, randomize] = useRandomVocab();
  const [current, setCurrent] = useState(0);
  const { listener, peek, reset, isComplete } = useQuizRecorder();
  const next = () => {
    reset();
    const newCurrent = current + 1;
    if (newCurrent === vocab.hanzi.length) {
      setCurrent(0);
      randomize();
    } else {
      setCurrent(newCurrent);
    }
  };
  return (
    <>
      <div className="m-md">
        <Title level={2}>Word:</Title>
        <b>{vocab.pinyin}</b>: {vocab.translations}
      </div>
      <div className="m-md">
        <div className="d-flex">
          <Title level={2}>Write It Down:</Title>
        </div>
        <div className="d-flex f-center">
          {vocab.hanzi
            .slice(0, current + (isComplete ? 1 : 0))
            .padEnd(vocab.hanzi.length, '？')
            .split('')
            .map((char, index) => <div key={index} className={classnames("hanzi-md m-sm b-1", { 'b-primary': current === index })}>{char}</div>)
          }
        </div>
        <div className="d-flex f-center my-xss">
          <Writer
            key={[current, vocab.hanzi[current]].join()}
            className="bg-light b-1"
            showOutline={peek.isPeeking}
            character={vocab.hanzi[current]}
            size={256}
            quiz
            {...listener}
          />
        </div>
      </div>
      <div className="d-flex mx-md my-lg">
        <Button
          className="mr-sm"
          disabled={isComplete}
          onMouseDown={peek.startPeeking}
          onMouseLeave={peek.endPeeking}
          onMouseUp={peek.endPeeking}
          icon={peek.isPeeking ? <EyeFilled /> : <EyeInvisibleFilled />}
        />
        <Button type="primary" className="f-grow" onClick={next} disabled={!isComplete}>
          Next Character
        </Button>
      </div>
    </>
  );
};
