import { useState } from 'react';

import { useQuizRecords } from '../../QuizRecord';
import { calculateStatisticSummary } from '../StatisticsPage/calculateStatistics';
import { randomItem, avg } from '../../helpers/math';
import { USER_VOCABS } from '../../modules/userVocab/data';

export function useRandomVocab(): [Vocab, () => void] {
  const [vocab, setVocab] = useState(() => randomItem(USER_VOCABS));
  const [records] = useQuizRecords();

  const randomize = () => {
    let nextVocab: Vocab;
    const summary = calculateStatisticSummary(records);
    do {
      nextVocab = randomItem(USER_VOCABS);
      const accuracy = avg(
        nextVocab.hanzi.split('').map((character) =>
          summary.characterSummaries.find((summary) => summary.character === character)?.statistic.accuracy || 0
        )
      );
      // make high accuracy text appear less. add 0.1 to make 100% accuracy still able to appear
      if (Math.random() + 0.1 < accuracy) {
        continue;
      }
    } while (nextVocab === vocab);
    setVocab(nextVocab);
  }
  return [vocab, randomize];
}