import moment from 'moment';
import { roundDecimal } from './math';

export function renderPercent(x: number) {
  const percent = x * 100;
  return roundDecimal(percent, 1) + '%';
}

export function renderDate(x: number) {
  return moment(x).locale('zh_cn').format('lll');
}

export function renderDuration(x: number) {
  return roundDecimal(x / 1000, 2) + 's';
}
