import { createContextWithHook } from 'react-maid';
import React, { FC, useState } from 'react';
import { useCurrentUser } from './auth/useCurrentUser';
import { useFirebase } from '../firebase';

interface Storage {
  data: any;
  clear: () => void;
  setItem: (key: string, value: any) => void;
  getItem: (key: string) => any;
  removeItem: (key: string) => any;
}

export const [StorageContext, useStorageContext] = createContextWithHook<Storage>('StorageContext');

interface LocalStorageProviderProps {
  storageKey?: string;
}

const LOCAL_STORAGE_AVAILBLE = typeof localStorage !== 'undefined';
function save(storageKey: string, data: any) {
  if (LOCAL_STORAGE_AVAILBLE) {
    localStorage.setItem(storageKey, JSON.stringify(data));
  }
}
function get(storageKey: string) {
  if (LOCAL_STORAGE_AVAILBLE) {
    const data = localStorage.getItem(storageKey);
    if (data) {
      return JSON.parse(data);
    }
  }
  return {};
}

export const FirebaseStorageProvider: FC = ({ children }) => {
  const firebase = useFirebase();
  const currentUser = useCurrentUser();
  const [data, setData] = useState<Dictionary<any>>(() => {
    return firebase.firestore().collection('users').doc(currentUser.uid).get();
  });

  const setAndSaveData = (newData: any) => {
    setData(newData);
    firebase.firestore().collection('users').doc(currentUser.uid).set(newData);
  };

  const setItem = (key: string, value: any) => {
    setAndSaveData({ ...data, [key]: value });
  };

  const getItem = (key: string) => {
    return data[key];
  };

  const removeItem = (key: string) => {
    const newData = { ...data };
    delete newData[key];
    setAndSaveData(newData);
  };

  const clear = () => {
    setAndSaveData({});
  };

  return (
    <StorageContext.Provider value={{ data, setItem, getItem, removeItem, clear }}>
      {children}
    </StorageContext.Provider>
  );
}

export const LocalStorageProvider: FC<LocalStorageProviderProps> = ({ storageKey = 'storage', children }) => {
  const [data, setData] = useState<Dictionary<any>>(() => {
    return get(storageKey);
  });

  const setAndSaveData = (newData: any) => {
    setData(newData);
    save(storageKey, newData);
  };

  const setItem = (key: string, value: any) => {
    setAndSaveData({ ...data, [key]: value });
  };

  const getItem = (key: string) => {
    return data[key];
  };

  const removeItem = (key: string) => {
    const newData = { ...data };
    delete newData[key];
    setAndSaveData(newData);
  };

  const clear = () => {
    setAndSaveData({});
  };

  return (
    <StorageContext.Provider value={{ data, setItem, getItem, removeItem, clear }}>
      {children}
    </StorageContext.Provider>
  );
};

export function useStorage<T>(key: string): [T | undefined, (value: T) => void];
export function useStorage<T>(key: string, defaultValue: T): [T, (value: T) => void];
export function useStorage<T>(key: string, defaultValue?: T): [T, (value: T) => void] {
  const storage = useStorageContext();
  const setItem = (value: T) => storage.setItem(key, value);
  return [storage.getItem(key) || defaultValue, setItem];
}
