import { useRecorder, Record, useStoredRecorder, useTimer } from './hooks';
import { StrokeData, SummaryData } from 'hanzi-writer';
import { useState } from 'react';
import { useStorage } from './Storage';

export interface QuizData {
  character: string;
  peeks: Record<number>[];
  mistakes: Record<number>[];
  correctStrokes: Record<number>[];
}

export type QuizRecords = Record<QuizData>[];

const QUIZ_STORAGE_KEY = 'QuizData';

export function useQuizRecords() {
  return useStorage<QuizRecords>(QUIZ_STORAGE_KEY, []);
}

export function useQuizRecorder() {
  const [isPeeking, peeks, startPeeking, endPeeking, clearPeeks] = useTimer();
  const [mistakes, recordMistake, clearMistakes] = useRecorder<number>();
  const [correctStrokes, recordCorrectStroke, clearCorrectStrokes] = useRecorder<number>();
  const [, recordQuiz] = useStoredRecorder<QuizData>(QUIZ_STORAGE_KEY);
  const [isComplete, setIsComplete] = useState(false);

  const onCorrectStroke = (summary: StrokeData) => {
    recordCorrectStroke(summary.strokeNum);
  };
  const onMistake = (summary: StrokeData) => {
    recordMistake(summary.strokeNum);
  };
  const onComplete = (summary: SummaryData) => {
    setIsComplete(true);
    recordQuiz({
      character: summary.character,
      mistakes,
      correctStrokes,
      peeks
    });
  };

  const reset = () => {
    setIsComplete(false);
    clearPeeks();
    clearMistakes();
    clearCorrectStrokes();
  };

  return {
    listener: { onComplete, onMistake, onCorrectStroke },
    peek: { isPeeking, startPeeking, endPeeking },
    reset,
    isComplete
  };
}