import React, { Component, createRef } from 'react';
import HanziWriter, { SummaryData, StrokeData } from 'hanzi-writer';

interface WriterProps {
  size: number;
  character: string;
  showOutline?: boolean;
  className?: string;
  quiz?: boolean;
  onComplete?: (summary: SummaryData) => void;
  onCorrectStroke?: (strokeData: StrokeData) => void;
  onMistake?: (strokeData: StrokeData) => void;
}

// lazy to think about avoiding stale closure problem
export class Writer extends Component<WriterProps> {
  svgRef = createRef<SVGSVGElement>();
  hanziWriter: HanziWriter | null = null;

  componentDidMount() {
    if (!this.svgRef.current) {
      throw new Error('cannot find SVG ref');
    }
    const { character, showOutline, size, quiz } = this.props;
    this.hanziWriter = HanziWriter.create(this.svgRef.current, character, {
      width: size,
      height: size,
      padding: size / 10,
      showOutline,
      showCharacter: !quiz,
      drawingWidth: 32
    });
    if (quiz) {
      this.beginQuiz();
    }
  }

  componentDidUpdate() {
    this.syncOutline();
  }

  private syncOutline() {
    if (!this.hanziWriter) {
      throw new Error('Cannot find instance of HanziWriter');
    }
    const { showOutline } = this.props;
    if (showOutline) {
      this.hanziWriter.showOutline();
    } else {
      this.hanziWriter.hideOutline();
    }

  }

  private beginQuiz() {
    if (!this.hanziWriter) {
      throw new Error('Cannot find instance of HanziWriter');
    }
    this.hanziWriter.quiz({
      onComplete: (summary) => {
        if (this.props.onComplete) {
          this.props.onComplete(summary);
        }
      },
      onCorrectStroke: (strokeData) => {
        if (this.props.onCorrectStroke) {
          this.props.onCorrectStroke(strokeData);
        }
      },
      onMistake: (strokeData) => {
        if (this.props.onMistake) {
          this.props.onMistake(strokeData);
        }
      },

      highlightOnComplete: true,
      leniency: 1,
      showHintAfterMisses: true
    });
  }

  render() {
    const { size, className } = this.props;
    return <svg ref={this.svgRef} width={size} height={size} className={className} />;
  }
}
