import jsonData from './hsk.json';

interface HSK {
  level: number[];
  vocab: Vocab;
}

export const HSK_VOCABS_BY_LEVEL: Vocab[][] = jsonData;
export const HSK_DICTIONARY = HSK_VOCABS_BY_LEVEL
  .reduce<Dictionary<HSK>>((result, vocabsPerLevel, level) => {
    vocabsPerLevel.forEach((vocab) => {
      result[vocab.hanzi] = result[vocab.hanzi] || { level: [], vocab };
      result[vocab.hanzi].level.push(level);
    });
    return result;
  }, {});
