import userHSK from './user-hsk.json';
import userVocabs from './user-vocabs.json';

import { HSK_VOCABS_BY_LEVEL } from '../hsk';

export const USER_HSK = userHSK.reduce<Dictionary<number[]>>((result, [level, index]) => {
  result[level] = result[level] || [];
  result[level].push(index);
  return result;
},
  {});
export const USER_DEFINED_VOCABS = userVocabs;
export const USER_VOCABS = [
  userHSK.map(([level, index]) => HSK_VOCABS_BY_LEVEL[level][index]),
  USER_DEFINED_VOCABS
].flat();
