import { Card, Tabs, List, Tooltip } from 'antd';
import React from 'react';

import { Info } from '../components/Info';
import { USER_DEFINED_VOCABS, USER_HSK } from '../modules/userVocab/data';
import { HSK_VOCABS_BY_LEVEL } from '../modules/hsk';
import { CheckOutlined } from '@ant-design/icons';

export const WordsBankPage = () => {
  return (
    <>
      <Tabs size="small">
        <Tabs.TabPane tab="User" key="0">
          <VocabList vocabs={USER_DEFINED_VOCABS} />
        </Tabs.TabPane>
        {HSK_VOCABS_BY_LEVEL.map((vocabs, level) =>
          <Tabs.TabPane tab={`HSK ${level + 1}`} key={(level + 1).toString()}>
            <VocabList vocabs={vocabs} checkedList={USER_HSK[level]} />
          </Tabs.TabPane>
        )}
      </Tabs>
    </>
  );
}

const VocabList = ({ vocabs, checkedList }: { vocabs: Vocab[], checkedList?: number[] }) => {
  return (
    <List
      className="m-md"
      dataSource={vocabs.map((vocab, index) => ({ vocab, index }))}
      pagination={{ pageSize: 50, position: 'both' }}
      renderItem={({ vocab, index }) => {
        return (
          <List.Item key={vocab.hanzi}>

            <Card className="w-100">
              <div className="d-flex">
                <Tooltip title={vocab.pinyin}>
                  <div className="d-flex">
                    {vocab.hanzi.split('').map((character) =>
                      <div className="hanzi-md">{character}</div>
                    )}
                  </div>
                </Tooltip>
                <div className="f-grow" />
                {checkedList && (checkedList.includes(index) ? <CheckOutlined /> : `#${index + 1}`)}
              </div>
              <Info title="Definition">
                {vocab.translations.join('; ')}
              </Info>
            </Card>
          </List.Item>
        );
      }}
    />
  );
}
