import React from 'react';

import { Info } from '../../components/Info';
import { useQuizRecords } from '../../QuizRecord';
import { calculateStatisticSummary } from './calculateStatistics';
import { CharacterSummariesTable } from './CharacterSummaryTable';

export const StatisticsPage = () => {
  const [quizRecords] = useQuizRecords();
  const summary = calculateStatisticSummary(quizRecords);

  return (
    <>
      <div className="m-md">
        <div className="d-flex m-xs">
          <Info title="Quiz Taken" className="f-grow f-50">
            {summary.quizCount}
          </Info>
          <Info title="Characters Written" className="f-grow f-50">
            {summary.characterCount}
          </Info>
        </div>
        <div className="d-flex m-xs">
          <Info title="Alien" className="f-grow f-25">
            {summary.familiarityDistribution.alien}
          </Info>
          <Info title="Forgotten" className="f-grow f-25">
            {summary.familiarityDistribution.forgotten}
          </Info>
          <Info title="Familiar" className="f-grow f-25">
            {summary.familiarityDistribution.familiar}
          </Info>
          <Info title="Remember" className="f-grow f-25">
            {summary.familiarityDistribution.remember}
          </Info>
        </div>
      </div>
      <div className="my-md">
        <CharacterSummariesTable characterSummaries={summary.characterSummaries} />
      </div>
    </>
  );
}
